.movieDetailsButtonWrapper {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
}

.movieDetailsBtnDelete {
    background: red;
    color: white;
    border: none;
    padding: 12px 18px;
    font-size: 18px;
    border-radius: 6px;
}

.movieDetailsBtnEdit {
    text-decoration: none;
    border: none;
    padding: 12px 22px;
    border-radius: 6px;
    font-size: 18px;
    background-color: thistle;
    color: black;
}

.movieDetailsBtnLike {
    border-radius: 6px;
    padding: 10px 22px;
    font-size: 20px;
    position: relative;
    right: -420px;
    bottom: -15px;
    background-color: cadetblue;
    border: none;
}

.movieBtn {
    margin: 30px 5px 0 10px;
}

.movieBtn:hover {
    cursor: pointer;
}