.homePage {
    background: rgba(19, 20, 27, 0.72);
    width: 100%;
    height: 520px;
    padding: 40px 0;
    position: relative;
}

.moviesBackgroundImage {
    width: 100%;
    height: 300px;
}

.moviesForumTitle {
    font-size: 45px;
    color: white;
    font-weight: 200;
    position: absolute;
    left: 3px;
    bottom: 77px;
}

.moviesForumDescription {
    font-size: 15px;
    text-transform: uppercase;
    font-family: Trebuchet MS, sans-serif;
    color: white;
    font-weight: 150;
    position: absolute;
    left: 3px;
    bottom: 60px;
}