.comments {
    position: relative;
    left: 5px;
    top: 40px;
    width: 104%;
    min-height: 120px;
    margin-top: 0;
    border: none;
    border-radius: 10px;
    padding: 8px 25px;
    background: linear-gradient(to right, #d6d6d6, #ffffff);
}

.commentTitle {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-weight: 400;
    font-size: 30px;
}

.commentInput {
    padding: 10px;
    margin-bottom: 10px;
    font-size: 15px;
    border: none;
    border-radius: 5px;
}

.commentSubmit {
    background-color: rebeccapurple;
    color: white;
    padding: 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px;
}