.navListItem {
    list-style: none;
    font-size: 18px;
    padding: 12px 5px;
    margin: 15px 10px;
}

.navListLink {
    text-decoration: none;
    color: white;
    text-transform: uppercase;
}

.navListLink:hover {
    color: #80ced6;
    cursor: pointer;
}

.navListWrapper {
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
    align-items: center;
}

.searchFormWrapper {
    padding: 15px;
    margin-right: 5px;
}

.searchInput {
    height: 38.5px;
    border: none;
    background: rgba(19, 20, 27, 0.72);
    color: white;
    font-size: 15px;
    padding-left: 5px;
    margin-right: 1.2px;
}

.searchBtn {
    padding: 10px 15px;
    padding-bottom: 18px;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    border-radius: 0;
    background: darkgray;
    height: 40px;
    cursor: pointer;
    margin-left: 0;
}