.commentUser {
    width: 100%;
    height: 35px;
    margin-bottom: 30px;
}

.commentUserContent {
    font-size: 18px;
    font-weight: 400;
}

.commentUserName {
    font-size: 15px;
}

.deleteComment {
    position: absolute;
    right: 80px;
    font-size: 18px;
    border: none;
    color: red;
    margin-bottom: 5px;
    padding: 2px 10px;
}

.deleteComment:hover {
    cursor: pointer;
    animation: delete .5s forwards;
    background: #adccff;
}