.authView {
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
    background: linear-gradient(to right, #d6d6d6, #ffffff);
    perspective: 600px;
    height: 620px;
}

.cont {
    position: relative;
    width: 35%;
    height: 580px;
    margin: 50px auto;
    transition: all 0.4s linear;
}

.mainBox {
    width: 100%;
    height: 100%;
    text-align: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

.create {
    position: relative;
    width: 100%;
    height: 100%;
    padding: 18px 15px;
    background: rgba(19, 20, 27, 0.72);
}

.authTitle,
.user,
.email,
.pass {
    display: block;
}

.authTitle {
    text-align: center;
    color: #f7f3f2;
    font-weight: 400;
    font-size: 25px;
    margin-top: 20px;
    margin-bottom: 15%;
}

.user,
.pass,
.email {
    width: 85%;
    height: 45px;
    border: none;
    color: white;
    margin: auto;
    margin-top: 15px;
    padding-left: 10px;
    font-size: 20px;
    font-weight: 500;
    border-radius: 25px;
    margin-bottom: 0;
    background: rgba(158, 158, 158, 0.49);
}

.user::placeholder,
.email::placeholder,
.pass::placeholder {
    color: #fff;
    font-size: 16px;
    font-weight: 100;
    transition: all 0.4s ease;
}

.user::focus::placeholder,
.email::placeholder,
.pass:focus::placeholder {
    opacity: 0;
}

.auth {
    color: #fff;
    cursor: pointer;
    width: 85%;
    height: 50px;
    border: none;
    font-size: 18px;
    font-weight: 500;
    margin-top: 20%;
    background: #67c5e7;
    border-radius: 25px;
    transition: background 0.4s ease;
}

.auth:hover {
    background: #2196F3;
}