.movieCard {
    display: inline-block;
    margin-right: 45px;
    margin-top: 20px;
    min-width: 400px;
    min-height: 500px;
    border: 2px solid #404040;
    border-radius: 5px;
    position: relative;
    left: 20px;
    -webkit-box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.75);
}

.movieCardTitle {
    position: absolute;
    bottom: 80px;
    left: 10px;
    font-weight: bold;
    font-size: 25px;
    font-family: Brush Script MT, 'cursive';
}

.movieCardImage {
    width: 400px;
    height: 350px;
}

.movieCardButton {
    position: absolute;
    left: 10px;
    text-decoration: none;
    bottom: 40px;
    width: 60px;
    font-size: 16px;
    text-align: center;
    padding: 10px;
    border: 1px solid black;
    border-radius: 3px;
    background-color: cadetblue;
    color: white;
}

.movieCardButton:hover {
    cursor: pointer;
    box-shadow: 5px 10px #888888;
    background-color: darkorange;
}

.movieLikes {
    font-size: 20px;
    position: absolute;
    left: 275px;
    bottom: 35px;
    padding: 15px;
    border: none;
    border-radius: 7px;
}