.createPage {
    margin: 0;
    padding: 0;
    height: 600px;
    width: 100%;
    position: relative;
    background: linear-gradient(to right, #d6d6d6, #ffffff);
}

.createFormWrapper {
    position: absolute;
    left: 200px;
    top: 70px;
    height: 80%;
    border-radius: 10px;
    background-repeat: no-repeat;
    background-size: 100% 100%;
    background: rgba(19, 20, 27, 0.72);
    box-shadow: 0px 20px 50px 0px #000000;
}

.createFormTitle {
    text-align: center;
    margin-top: 50px;
    color: white;
    font-size: 25px;
    font-weight: 400;
}

.movieTitle,
.movieDescription,
.movieImageURL {
    background: rgba(19, 20, 27, 0.72);
    height: 45px;
    width: 82%;
    color: white;
    padding: 10px;
    margin-top: 10px;
    margin-left: 100px;
    font-size: 15px;
    border: none;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.movieDescription {
    height: 100px;
}

.createMovieBtn {
    position: absolute;
    bottom: 22px;
    left: 100px;
    padding: 14px 19px;
    font-size: 14px;
    font-weight: 500;
    transition: background 0.4s ease;
    background: #67c5e7;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

.createMovieBtn:hover {
    background: #2196F3;
}