.overlay {
    display: block;
    background-color: rgba(19, 20, 27, 0.72);
    backdrop-filter: blur(5px);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
}

.modal {
    background: rgba(19, 20, 27, 0.72);
    color: white;
    width: 700px;
    height: 250px;
    text-align: center;
    margin: auto;
    margin-top: 15vh;
    padding: 45px;
}

.action {
    display: inline-block;
    background-color: #acd0c0;
    padding: 8px 16px;
    text-decoration: none;
    color: black;
    font-size: 20px;
    margin-right: 15px;
}

.action:hover {
    background-color: #d9b44a;
    color: white;
    cursor: pointer;
}

.message {
    margin-bottom: 45px;
    font-size: 25px;
}