.movieContainer {
    position: absolute;
    min-width: 100%;
    min-height: 1200px;
    display: flex;
    flex-flow: column wrap;
    background: white;
}

.movieDetailsImg {
    height: 700px;
    width: 670px;
    padding: 35px;
    position: absolute;
    top: 80px;
    left: 5px;
}

.movieDetailsTitle {
    padding: 20px;
    margin-bottom: 0;
    font-size: 40px;
    color: #2F4F4F;
    font-weight: 500;
    line-height: 1.6;
    font-family: Tahoma, sans-serif;
}

.movieDetailsInfo {
    width: 42%;
    max-height: 250px;
    position: relative;
    left: 750px;
    top: -100px;
}

.movieDetailsInfoImg {
    text-align: center;
    font-size: 30px;
    font-family: Courier, monospace;
    font-weight: bold;
}

.movieDetailsInfoParagraph {
    line-height: 1.5;
    border-top: 3px solid black;
    padding: 15px 5px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: 20px;
    text-transform: capitalize;
    letter-spacing: 1px;
}